@import '../../../assets/css/theme';

.step_wrap_border {
  background: #33254e;
  border-radius: 6px;
  padding: 20px 20px 0 20px;
  :global {
    .ant-steps-item-process {
      .ant-steps-item-container {
        .ant-steps-item-icon {
          background: rgba(193, 160, 255, 0.2);
          border-color: transparent;
          .ant-steps-icon {
            color: #c1a0ff;
          }
        }
      }
    }
    .ant-steps-item-finish {
      .ant-steps-item-container {
        .ant-steps-item-icon {
          .ant-steps-icon {
            color: #fff;
          }
        }
        .ant-steps-item-tail {
          &::after {
            background-color: rgba(255, 255, 255, 0.3);
          }
        }
      }
    }
  }
}
