@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.info_list {
  &_item {
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &:last-child {
      margin-bottom: 30px;
    }
  }
  &_label {
    font-weight: 400;
    color: $color-text-default-5;
    margin-right: 10px;
    white-space: nowrap;
    @include responseFont();
  }
  &_value {
    @include responseFont();
    @include mobile-only {
      width: 100%;
    }
    font-weight: 600;
    &_default {
      color: $color-text-default;
    }
    &_warning {
      color: $warning;
    }
    &_error {
      color: $error;
    }
  }
}
.swap_tip {
  font-size: 12px;
  font-weight: 400;
  color: $color-text-default-5;
  line-height: 17px;
  margin: 30px 0;
}
.price_list {
  background: $color-info-bg;
  border-radius: 5px;
  border: 1px solid $color-border-secondary;
  padding: 0 16px;
  margin-bottom: 20px;
  margin-top: 20px;

  &_item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid $color-border-secondary;
    padding: 16px 0;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
    @include responseFont(16px, 14px);
    flex-wrap: wrap;
    > img {
      width: 20px;
      height: 20px;
      margin-right: 10px;
      border-radius: 50%;
      border: 1px solid #ddd;
      background: #fff;
    }
    &:last-child {
      border: none;
    }
  }
  &_right {
    @include mobile-only {
      width: 100%;
      margin-top: 5px;
    }
    margin-left: auto;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
  }
}
.swap_value {
  font-family: Poppins-Bold;
  font-weight: 700;
  color: #fff;
  margin: 0 6px;
  @include responseFont(24px, 20px);
}
.swap_symbol {
  display: inline-block;
  position: relative;
  top: -1px;
  font-size: 14px;
  font-family: Poppins-Bold;
  font-weight: 700;
  color: #fff;
  line-height: 21px;
  margin: 0 4px;
  @include responseFont();
}
.error_msg {
  font-size: 12px;
  font-weight: 400;
  color: $warning;
  line-height: 17px;
  text-align: left;
  margin-bottom: 10px;
}
.error_icon {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: rgba($warning, 0.2);
  border-radius: 50%;
  text-align: center;
  line-height: 15px;
  color: $warning;
  font-weight: bold;
  margin-right: 10px;
}
.success {
  // padding-top: 22px;
  text-align: center;
  img {
    margin-bottom: 20px;
    width: 80px;
    height: 80px;
  }
}
.recipient {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  color: $color-text-default-7;
  line-height: 20px;
  margin-top: 10px;
  text-align: center;
  &_tip {
    font-size: 12px;
    font-family: Poppins;
    font-weight: 400;
    color: #9e9abc;
    line-height: 18px;
    margin-top: 10px;
    text-align: left;
  }
}
.token_swap_info {
  text-align: center;
  @include mobile-only {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.plus {
  @include mobile-only {
    display: block;
  }
}