@import '../../assets/css/mixins';

.token_level {
  @include darkThemeOnly {
    background: rgba(126, 146, 179, 0.1);
  }
  @include lightThemeOnly {
    background: rgba(9, 29, 63, 0.1);
  }

  border-radius: 4px;
  display: inline-block;
  > span {
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    font-style: normal;
    vertical-align: middle;
    padding: 0 10px 0px 6px;
    cursor: pointer;
  }
}
.token_level_untip {
  > span {
    cursor: default;
  }
  .level_img {
    cursor: default;
  }
}

.level_img {
  width: 20px;
  vertical-align: middle;
  margin-left: -6px;
  cursor: pointer;
}

.level_tip {
  :global {
    .ant-tooltip-arrow:before {
      @include darkThemeOnly {
        background-color: rgba(29, 19, 46, 0.8) !important;
      }
      @include lightThemeOnly {
        background-color: rgba(153, 153, 153, 0.95) !important;
      }
    }
    .ant-tooltip-content .ant-tooltip-inner {
      @include darkThemeOnly {
        background-color: rgba(29, 19, 46, 0.8) !important;
        color: rgba(255, 255, 255, 0.5);
      }
      @include lightThemeOnly {
        background-color: rgba(153, 153, 153, 0.95) !important;
        color: rgba(255, 255, 255, 1);
      }
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      text-align: left;
      font-style: normal;
      padding: 10px;
      > div:nth-child(1) {
        background: rgba(216, 220, 223, 0.1);
        // span {
        //   color: #d8dcde;
        // }
      }
      a {
        color: $color-a;
      }
    }
  }
}

// Unknown
.level_text_0 {
  @include darkThemeOnly {
    color: #959ca6;
  }
  @include lightThemeOnly {
    color: #48628b;
  }
}

// Neutral
.level_text_1 {
  @include darkThemeOnly {
    color: #bd7238;
  }
  @include lightThemeOnly {
    color: #a06334;
  }
}

// OK
.level_text_2 {
  @include darkThemeOnly {
    color: #3cb23b;
  }
  @include lightThemeOnly {
    color: #56c755;
  }
}

// Suspicious
.level_text_3 {
  @include darkThemeOnly {
    color: #caa83d;
  }
  @include lightThemeOnly {
    color: #ccae52;
  }
}

// Unsafe
.level_text_4 {
  @include darkThemeOnly {
    color: #b9473a;
  }
  @include lightThemeOnly {
    color: #d87064;
  }
}

// SunPump
.level_text_5 {
  @include darkThemeOnly {
    color: #b995ff;
  }
  @include lightThemeOnly {
    color: #6f49ba;
  }
}
